.headerContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 91px;
  background-color: #21222A;
}

.headerLogoArea {
  display: flex;
  justify-content: flex-start;
}

.headerLogoArea span {
  color: #61DAFB;
  font-size: 22px;
  font-weight: 700;
  letter-spacing: -0.05em;
  line-height: 27px;
  margin-left: 6.5px;
}

.headerTitleArea {
  display: flex;
  justify-content: flex-end;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  color: #DEEBF8;
}
