.contentContainer {
  flex-grow: 1;
  background-color: #282D35;
  color: white;
}

.contentContainer h1 {
  font-weight: 700;
  font-size: 39;
  line-height: 47px;
  letter-spacing: -0.05em;
  margin-top: 57px;
  margin-bottom: 46px;
}

.contentContainer ul {
  list-style: none;
}

.contentContainer ul li {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  margin-bottom: 20px;
}

.contentContainer ul li::before {
  content: "\2022";
  color: #61DAFB;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}
